const STORAGE_KEY = 'archaeo_system_id';

/**
 * ØªÙˆÙ„ÛŒØ¯ Ø´Ù†Ø§Ø³Ù‡ Ù¾Ø§ÛŒØ¯Ø§Ø± Ø¯Ø³ØªÚ¯Ø§Ù‡ Ø¨Ø§ Ø§Ø³ØªÙØ§Ø¯Ù‡ Ø§Ø² Ù…Ø´Ø®ØµØ§Øª Ø³Ø®Øªâ€ŒØ§ÙØ²Ø§Ø±ÛŒ
 * ØªÙˆØ³Ø· ØªÛŒÙ… ÙØ±Ø§Ù†Øªâ€ŒØ§Ù†Ø¯ Ùˆ Ø§Ù…Ù†ÛŒØª
 */
export const generateStableSystemId = (): string => {
  // Ø¬Ù…Ø¹â€ŒØ¢ÙˆØ±ÛŒ Ø§Ø·Ù„Ø§Ø¹Ø§Øª Ø«Ø§Ø¨Øª Ø¯Ø³ØªÚ¯Ø§Ù‡
  const components: (string | number)[] = [
    navigator.language || 'en',
    screen.width,
    screen.height,
    screen.colorDepth,
    navigator.hardwareConcurrency || 2,
    navigator.platform || 'unknown',
    new Date().getTimezoneOffset(),
  ];

  // ØªÙ„Ø§Ø´ Ø¨Ø±Ø§ÛŒ Ø¯Ø±ÛŒØ§ÙØª Ø§Ø·Ù„Ø§Ø¹Ø§Øª WebGL (Ø§Ø®ØªÛŒØ§Ø±ÛŒ)
  try {
    const canvas = document.createElement('canvas');
    const gl = canvas.getContext('webgl');
    if (gl) {
      const renderer = gl.getParameter(gl.RENDERER);
      components.push(renderer);
    }
  } catch {
    // ignored
  }

  // Ø§ÛŒØ¬Ø§Ø¯ Ù‡Ø´ Ø³Ø§Ø¯Ù‡ Ø§Ø² ØªØ±Ú©ÛŒØ¨ Ø§Ø·Ù„Ø§Ø¹Ø§Øª
  const hash = components.join('|');
  let id = '';
  
  for (let i = 0; i < hash.length; i++) {
    id += hash.charCodeAt(i).toString(16);
  }
  
  // ÙÙ‚Ø· Û¹ Ø±Ù‚Ù… Ø§ÙˆÙ„ Ø¨Ø§ Ù¾ÛŒØ´ÙˆÙ†Ø¯ ME-
  return `ME-${id.slice(0, 9).toUpperCase()}`;
};

/**
 * Ø¯Ø±ÛŒØ§ÙØª ÛŒØ§ Ø§ÛŒØ¬Ø§Ø¯ Ø´Ù†Ø§Ø³Ù‡ Ø¯Ø³ØªÚ¯Ø§Ù‡
 * Ø°Ø®ÛŒØ±Ù‡ Ø´Ø¯Ù‡ Ø¯Ø± localStorage Ø¨Ø±Ø§ÛŒ Ù…Ø§Ù†Ø¯Ú¯Ø§Ø±ÛŒ
 */
export const getOrCreateSystemId = (): string => {
  try {
    let id = localStorage.getItem(STORAGE_KEY);
    
    if (!id) {
      id = generateStableSystemId();
      localStorage.setItem(STORAGE_KEY, id);
    }
    
    return id;
  } catch (error) {
    // Fallback Ø¨Ø±Ø§ÛŒ Ø­Ø§Ù„Øªâ€ŒÙ‡Ø§ÛŒ Ø®Ø·Ø§ (Ù…Ø«Ù„ Private Mode)
    console.warn('Error accessing localStorage:', error);
    return generateStableSystemId();
  }
};